import { ServicePluginDefinition } from '@wix/sdk-types';
import {
  Context,
  GetRecommendationsRequest,
  GetRecommendationsResponse,
} from './service-plugins-types.js';
import {
  renameKeysFromSDKRequestToRESTRequest,
  renameKeysFromRESTResponseToSDKResponse,
} from '@wix/sdk-runtime/rename-all-nested-keys';

export interface GetRecommendationsEnvelope {
  request: GetRecommendationsRequest;
  metadata: Context;
}

export const provideHandlers = ServicePluginDefinition<{
  /**
   *
   * This method retrieves an array containing the lists of recommended items returned by each algorithm. Items must be selected from catalogs in Wix apps installed on the site.
   *
   * If a requested recommendation algorithm's type is `RELATED_ITEMS` then the `items` field is included in the request. The response should contain items related to those submitted.
   * How the related items are selected depends on the recommendation algorithms used. For example, items can be in the same category, or can be frequently bought or watched together. */
  getRecommendations(
    payload: GetRecommendationsEnvelope
  ): GetRecommendationsResponse | Promise<GetRecommendationsResponse>;
}>('ECOM_RECOMMENDATIONS_PROVIDER', [
  {
    name: 'getRecommendations',
    primaryHttpMappingPath: '/v1/get-recommendations',
    transformations: {
      toREST: (payload: any) => {
        const toRestResponse = payload;

        return renameKeysFromSDKRequestToRESTRequest(toRestResponse);
      },
      fromREST: (payload: any) => {
        const fromRestRequest = payload;

        return renameKeysFromRESTResponseToSDKResponse(fromRestRequest);
      },
    },
  },
]);
