import {
  RecommendationAlgorithmNotSupported,
  ItemAppIdNotSupportedError,
} from './service-plugins-types.js';

export class AlgorithmNotSupportedWixError extends Error {
  /** @hidden */
  httpCode: number;
  /** @hidden */
  statusCode: string;
  /** @hidden */
  applicationCode: string;
  /** @hidden */
  name: string;
  /** @hidden */
  errorSchemaName: string;
  /** @hidden */
  errorType: string;
  /** @hidden */
  spiErrorData: object;

  data: RecommendationAlgorithmNotSupported;

  constructor(data: RecommendationAlgorithmNotSupported = {}) {
    super('AlgorithmNotSupportedError');

    this.httpCode = 400;
    this.statusCode = 'INVALID_ARGUMENT';
    this.applicationCode = 'ALGORITHM_NOT_SUPPORTED';
    this.name = 'AlgorithmNotSupportedError';
    this.errorSchemaName =
      'com.wix.ecom.recommendations.spi.RecommendationAlgorithmNotSupported';
    this.errorType = 'SPI';
    this.spiErrorData = {
      name: 'AlgorithmNotSupportedError',
      applicationCode: 'ALGORITHM_NOT_SUPPORTED',
    };

    this.data = data;
  }

  /** @hidden */
  static readonly __type = 'wix_spi_error';
}

